a.document-link {
  font-size: small;
  color: var(--black);
  cursor: pointer;
}
a.document-link i {
  top: 391px;
  left: 502px;
  margin: 8px;
  width: 14px;
  height: 19px;
  opacity: 1;
  color: var(--blue);
}

a.document-link:hover:not(.rejected),
a.document-link:hover:not(.rejected) * {
  color: var(--blue) !important;
}

a.document-link.accepted *,
a.document-link.accepted {
  color: var(--blue);
}
a.document-link.accepted {
  text-decoration: underline;
}

a.document-link.rejected *,
a.document-link.rejected {
  color: #f23838;
}

.document-icon {
  position: relative;
}
.document-icon .file-default {
  transform: scale(1.2, 1.4);
  position: relative;
  top: -2px;
  left: -2px;
}

.document-icon .external-icon {
  position: absolute;
  font-size: 5px;
  top: 7px;
  left: 19px;
  padding: 0px !important;
  margin: 0px !important;
  opacity: 0;
  transition: opacity 0.4s;
}

.document-icon .external-icon.delete {
  top: 7px;
  left: 19px;
}

.document-link:hover .document-icon .external-icon {
  opacity: 1;
}
