.card-content {
  line-height: 1.3rem;
}
.card-view-btn {
  background: none;
  border: none;
  color: #00a3ff;
  padding-left: 20px;
  font-size: 12px;
}
.card-view-btn:hover {
  background: none;
  border: none;
  color: blue;
  padding-left: 20px;
}
.card-view-btn:active {
  background: none;
  border: none;
  color: blue;
  padding-left: 20px;
}
.card-view-btn:visited {
  background: none;
  border: none;
  color: blue;
  padding-left: 20px;
}
.card-border {
  border-color: transparent;
  box-shadow: 0px 3px 6px var(--shadow-primary);
}

.card-view-btn:not(:disabled):not(.disabled).active:focus,
.card-view-btn:not(:disabled):not(.disabled):active:focus,
.show > .card-view-btn.dropdown-toggle:focus {
  color: blue;
  background-color: transparent;
  border: transparent;
}
