@import "./../../../styles/color.css";

.loan-application-modal .modal-dialog {
  max-width: 90% !important;
  min-width: 90% !important;
  width: 90% !important;
}

.loan-application-modal .modal-body {
  min-height: 90vh;
  padding: 15px 40px;
}

.loan-application-tab-container {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  flex: 1;
  border-bottom: 0px;
}

.loan-application-tab-container > li button {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  border: 0px !important;
  background-color: transparent !important;
}

.loan-application-tab-container > li {
  width: 16.66666666666667%;
  display: flex;
  justify-content: center;
  padding-bottom: 10px;
}

.loan-application-tab-container > li:first-child {
  justify-content: flex-start;
}

.loan-application-tab-container > li:last-child {
  justify-content: flex-end;
}

.loan-application-tab-container > li button * {
  cursor: pointer;
  margin-bottom: 0px !important;
  font-size: 12px;
  font-weight: 600;
}

.loan-application-tab-container > li button {
  color: var(--secondary);
  padding-left: 0px !important;
  padding-right: 0px !important;
}

.loan-application-tab-container > li button.active {
  color: var(--green) !important;
}

.loan-application-tab-container > li button .badge {
  display: inline-block;
  padding: 12px 12px;
  text-align: center;
  font-size: 12px;
  font-weight: bold;
  letter-spacing: 0px;
  color: var(--white);
  opacity: 1;
  background: var(--secondary) 0% 0% no-repeat padding-box !important;
  border-radius: 50% !important;
  height: 32px;
  width: 32px;
}

.loan-application-tab-container > li button.active .badge {
  background-color: var(--green) !important;
}

.loan-application-tab-container > li button.active:not(.text-green) label {
  color: var(--black) !important;
}

.loan-application-tab-container > li button > label {
  color: var(--secondary);
  text-align: left;
  font-size: 12px;
  letter-spacing: 0px;
  text-transform: uppercase;
  opacity: 1;
}

.loan-application-btn {
  border-radius: 10px;
  padding: 8px 30px;
}

.btn-white {
  background: var(--white) 0% 0% no-repeat padding-box;
  border: 1px solid var(--secondary);
  color: var(--black);
}

.btn-white:hover {
  background: var(--dark-white) 0% 0% no-repeat padding-box;
  border: var(--secondary) 1px solid;
  color: var(--green);
}

.btn-white.focus,
.btn-white:focus {
  color: #fff;
  background-color: var(--dark-white);
  border: 1px solid var(--secondary);
  color: var(--green);
  box-shadow: none !important;
}

.btn-white:not(:disabled):not(.disabled).active,
.btn-white:not(:disabled):not(.disabled):active,
.show > .btn-white.dropdown-toggle {
  color: var(--green);
  background-color: var(--dark-white);
  border: 1px solid var(--secondary);
}

/* buttons */

.btn-default {
  background: var(--blue) 0% 0% no-repeat padding-box;
  border: 1px solid var(--blue);
  color: var(--white);
}

.btn-default:hover {
  background: var(--dark-white) 0% 0% no-repeat padding-box;
  border: var(--blue) 1px solid;
  color: var(--blue) l;
}

.btn-default.focus,
.btn-default:focus {
  color: #fff;
  background-color: var(--dark-white);
  border: 1px solid var(--blue);
  color: var(--blue);
  box-shadow: none !important;
}

