/* primary */

.btn-primary {
  background: var(--green) 0% 0% no-repeat padding-box;
  border: 1px solid var(--green);
}

.btn-primary:hover {
  background: var(--dark-white) 0% 0% no-repeat padding-box;
  border: var(--green) 1px solid;
  color: var(--green) l;
}

.btn-primary.focus,
.btn-primary:focus {
  color: #fff;
  background-color: var(--dark-white);
  border: 1px solid var(--green);
  color: var(--green);
  box-shadow: none !important;
}

.btn-primary:not(:disabled):not(.disabled).active,
.btn-primary:not(:disabled):not(.disabled):active,
.show > .btn-primary.dropdown-toggle {
  color: var(--green);
  background-color: var(--dark-white);
  border: 1px solid var(--green);
}

/* danger */

.btn-danger {
  background: var(--red) 0% 0% no-repeat padding-box;
  border: 1px solid var(--red);
}

.btn-danger:hover {
  background: var(--dark-white) 0% 0% no-repeat padding-box;
  border: var(--red) 1px solid;
  color: var(--red) l;
}

.btn-danger.focus,
.btn-danger:focus {
  color: #fff;
  background-color: var(--dark-white);
  border: 1px solid var(--red);
  color: var(--red);
  box-shadow: none !important;
}

.btn-danger:not(:disabled):not(.disabled).active,
.btn-danger:not(:disabled):not(.disabled):active,
.show > .btn-danger.dropdown-toggle {
  color: var(--red);
  background-color: var(--dark-white);
  border: 1px solid var(--red);
}

/* secondary */

.btn-secondary {
  background: var(--secondary) 0% 0% no-repeat padding-box;
  border: 1px solid var(--secondary);
}

.btn-secondary:not([disabled]):hover {
  background: var(--dark-white) 0% 0% no-repeat padding-box;
  border: var(--secondary) 1px solid;
  color: var(--secondary) l;
}

.btn-secondary.focus,
.btn-secondary:focus {
  color: #fff;
  background-color: var(--dark-white);
  border: 1px solid var(--secondary);
  color: var(--secondary);
  box-shadow: none !important;
}

.btn-secondary:not(:disabled):not(.disabled).active,
.btn-secondary:not(:disabled):not(.disabled):active,
.show > .btn-secondary.dropdown-toggle {
  color: var(--secondary);
  background-color: var(--dark-white);
  border: 1px solid var(--secondary);
}

.form-section-header {
  font-size: 20px;
  letter-spacing: 0px;
  opacity: 0.7;
  margin-left: 3%;
  font-weight: 600;
}

.form-label {
  color: var(--black) !important;
  font: normal normal normal 14px/20px Gilroy-Medium;
  letter-spacing: 0px;
  opacity: 0.5;
}
.form-control:focus {
  color: #495057 !important;
  background-color: #fff !important;
  border-color: none !important;
  outline: 0 !important;
  box-shadow: none !important;
}

input[type="text"].form-control,
input[type="number"].form-control,
input[type="date"].form-control,
select.form-control {
  background: #fcfeff 0% 0% no-repeat padding-box !important;
  border: 1px solid #c5d8e3 !important;
  border-radius: 12px !important;
  font: normal normal normal 14px/20px Gilroy-Medium;
  letter-spacing: 0px;
  color: var(--black) !important;
}

.br-12 {
  border-radius: 12 px !important;
}

//show interest table start
.show-interest-quotes {
  color: var(--black);
  font-size: 20px;
  text-align: center;
  letter-spacing: 0px;
  font: normal normal normal 20px/29px Gilroy-Bold;
}

.like-image-span {
  display: inline-block;
  width: 80px;
  padding: 20px;
  background-color: #dbecf985;
  border-radius: 100%;
  text-align: center;
  transform: scale(0.6);
}

//show interest table end

.loan-application-progress-bar.progress {
  height: 2px;
  position: relative;
  width: 100%;
  left: 0%;
  right: 0%;
  top: 75px;
}

.loan-application-progress-bar.progress > div {
  background-color: var(--green) !important;
}

.ph-10 {
  padding: 0px 4%;
}

.default-badge {
  background-color: transparent !important;
  padding: 0px;
  font-weight: 400;
}

.heading {
  font-weight: 700 !important;
  font-size: 20px;
}
// documents styles ends

.tab-nav-button-container {
  position: absolute;
  bottom: 10%;
  right: 3%;
}

.show-interest-heart {
  padding: 10px !important;
  color: #00a3ff;
  font-size: x-large;
  background-color: #e7f6ff;
}

.show-interest-text {
  color: #000000;
  font-size: 20px;
  font: normal normal normal 20px/29px Gilroy-Bold;
}

.color-black {
  color: var(--black);
}

button.close {
  font-size: 2rem !important;
}

.rejection-modal {
  max-width: 45%;
}

.svg {
  width: 32px;
  height: 32px;
  background: var(--shadow-border);
  border-radius: 12px;
}
.custom-label {
  display: inline-block;
  padding-left: 2.5em;
  position: relative;
  cursor: pointer;
}
.select-input[type="checkbox"] {
  opacity: 0;
  width: 0px;
  height: 0px;
}
label {
  transition: background-color 200ms linear;
}
label .svg {
  transition: border-color 80ms linear;
  background: #f2f2f2;
}
label .svg:hover {
  cursor: pointer;
}
label .svg path {
  transition: all 80ms linear;
  stroke: #fff;
  transform: translate(10px, 10px) scale(0.8);
}
.select-input[type="checkbox"]:checked ~ label .svg path {
  stroke-dashoffset: 0;
  stroke: var(--green);
}
.select-input[type="checkbox"]:checked ~ label .svg {
  background: var(--bg-muted);
}

@media (min-width: 576px) {
  .rejection-modal .modal-dialog {
    max-width: 669px;
    margin: 1.75 rem auto;
  }
}

textarea {
  border-radius: 12px !important;
}

.rejection-modal-container {
  background-color: rgba(0, 0, 0, 0.6);
}

.w-28 {
  max-width: 28%;
}

.show-interest-back-btn {
  position: absolute;
  right: 3%;
  bottom: 10%;
}

button.text-green > label {
  color: var(--green) !important;
}

.lender-loans-tab.nav-tabs {
  border-bottom: 0px;
  display: flex;
}

.lender-loans-tab.nav-tabs .nav-item {
  padding: 20px 10px;
}

.lender-loans-tab.nav-tabs .nav-item button {
  border: 0px;
  background-color: var(--tab-default);
  border-radius: 12px;
  font-size: 16px;
  font-weight: 600;
  letter-spacing: 0px;
  color: var(--black);
  text-transform: uppercase;
  opacity: 0.7;
  width: 100%;
  text-align: center;
  padding: 8px 30px;
}

.lender-loans-tab.nav-tabs .nav-item button:hover,
.lender-loans-tab.nav-tabs .nav-item button.active {
  opacity: 1;
  background-color: var(--tab-blue);
}
