@import 'Base';
@import 'Font';
@import 'Mixins';
@import 'componentsStyle/index';

@font-face {
  font-family: 'SFUIText-Regular';
  src: url('../styles/Fonts/SFUIText-Regular.otf') format('opentype');
}

@font-face {
  font-family: 'SFUIText-Bold';
  src: url('../styles/Fonts//SFUIText-Bold.otf') format('opentype');
}

@font-face {
  font-family: 'SFUIText-Semibold';
  src: url('../styles/Fonts/SFUIText-Semibold.otf') format('opentype');
}

@font-face {
  font-family: 'SFUIText-Medium';
  src: url('../styles/Fonts/SFUIText-Medium.otf') format('opentype');
}

/* Search bar */
.search-container {
  width: 80%;
  display: block;
  margin: 10px;
}

input#search-bar {
  margin: 0 auto;
  width: 100%;
  height: 45px;
  padding: 0 20px;
  font-size: 1rem;
  border: 1px solid #eaf6fd;
  border-radius: 10px;
  outline: none;
  &:focus {
    border: 1px solid #008abf;
    transition: 0.35s ease;
    color: #008abf;
    &::-webkit-input-placeholder {
      transition: opacity 0.45s ease;
      opacity: 0;
    }
    &::-moz-placeholder {
      transition: opacity 0.45s ease;
      opacity: 0;
    }
    &:-ms-placeholder {
      transition: opacity 0.45s ease;
      opacity: 0;
    }
  }
}

.search-icon {
  position: relative;
  float: right;
  width: 75px;
  height: 75px;
  top: -30px;
  right: -50px;
  color: var(--secondary);
}

.px-3rem {
  padding-left: 3rem !important;
  padding-right: 3rem !important;
}
