@import "./Base.css";
@import "./Components.css";
@import "./CommonStyles.css";
@import "./Font.css";
@import "./color.css";

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

* {
  box-sizing: border-box;
}

a {
  text-decoration: none;
}

input:focus,
select:focus,
textarea:focus,
button:focus {
  outline: none;
}

.main-container {
  background-color: var(--primary-bg);
  min-height: 100vh;
}

.h40 {
  height: 40vh;
  overflow-x: auto;
}

/* background colors */
.primary-bg {
  background-color: var(--primary-bg);
}
.badge-violet {
  background-color: var(--violet);
}
/* menuxl */
.dropdown-menu-xl {
  left: -26px;
  min-width: 20rem;
}

/* tables */
.table thead th {
  border-bottom: none;
  border-top: none;
  padding: 0px;
  text-align: left;
  padding-bottom: 16px;
}
.table td,
.table th {
  border-bottom: none;
  border-top: 1px #0000001a solid;
  padding: 0px;
  text-align: left;
  padding-top: 16px;
  padding-bottom: 16px;
}
.table tr:last-child {
  border-bottom: 0.5px #0000001a solid;
  padding-bottom: 16px;
}
/* display */
.flex {
  display: flex;
}
.dflex {
  display: flex;
  align-self: center;
  justify-content: center;
}
.dflex-center {
  display: flex;
  align-items: center;
  justify-content: center;
}
.align-item-center {
  display: flex;
  align-self: center;
}

.align-item-self-center {
  align-self: center;
}

/* height */
.h100 {
  height: 100vh;
  width: 100%;
}
.w100 {
  height: 100px;
  width: 400px;
}
/* .remove modal header border-line */
.modal-header {
  border-bottom: none !important;
}

/* .navbar */
.dropdown-toggle::after {
  display: none;
}
.text-decoration-none {
  text-decoration: none !important;
}
/* Font Awesome Icons have variable width. Added fixed width to fix that.*/
.icon-width {
  width: 2rem;
}

/* remove blue line from from control */
.form-control:focus {
  color: #495057;
  background-color: #fff;
  border-color: none !important;
  outline: 0;
  box-shadow: none !important;
}
.align-left {
  text-align: left;
}
/* searchbar */

::placeholder {
  color: #a1a1a1;
}

/* hide the placeholder text on focus */
:focus::placeholder {
  text-indent: -999px;
}

.searchInput:focus {
  outline: none;
  transform: scale(1.1);
  transition: all 0.1s ease-in-out;
}

.searchInputIcon {
  position: absolute;
  right: 0.8rem;
  top: 0.3rem;
  color: #a1a1a1;
  transition: all 0.1s ease-in-out;
  border: 0px !important;
  cursor: pointer;
}

.container:focus-within > .searchInputWrapper > .searchInputIcon {
  right: 0.2rem;
}

.searchInputLoader {
  position: absolute;
  right: 0.8rem;
  top: 0.6rem;
  transition: all 0.1s ease-in-out;
  cursor: pointer;
}

/* opacity */
.op70 {
  opacity: 0.7;
}
.op75 {
  opacity: 0.75;
}
.op100 {
  opacity: 1;
}

/* icons */
.icon-20 {
  height: 18px;
}

.a-link {
  color: var(--black);
  text-decoration: none;
}
.a-link:hover {
  color: var(--blue);
  text-decoration: none;
}
/* .conatiner */
@media (min-width: 1200px) {
  .container,
  .container-lg,
  .container-md,
  .container-sm,
  .container-xl {
    max-width: 1380px;
  }
}

/* width */
.w-60 {
  width: 60%;
}

.w-80 {
  width: 80%;
}

.dotted-box {
  padding: 10px;
  padding-top: 14px;
  padding-bottom: 14px;
  border: 1px dashed #707070;
}
/* curved lines */

@media only screen and (max-width: 720px) {
  .w-60,
  .w-80 {
    width: 100%;
  }
}

.new-custom-scroll tbody tr {
  display: table;
  width: 100%;
  table-layout: fixed;
}
.new-custom-scroll tbody {
  opacity: 0.5;
  height: 300px;
  overflow-y: scroll;
  display: block;
}
.new-custom-scroll thead {
  display: table;
  width: 100%;
  table-layout: fixed;
}
.new-custom-scroll::-webkit-scrollbar {
  width: 5px;
  height: 10px;
  transition: all 1s linear;
}
.draft-app {
  width: 50% !important;
}
.custom-scrollbar {
  overflow-y: overlay;
  overflow-x: overlay;
}
.custom-scrollbar::-webkit-scrollbar {
  width: 5px;
  height: 10px;
  transition: all 1s linear;
}

.custom-scrollbar::-webkit-scrollbar-track {
  background-color: transparent;
  position: absolute;
  border-radius: 10px;
}

.custom-scrollbar::-webkit-scrollbar-thumb {
  background-color: var(--secondary);
  border-radius: 10px;
}

.fpo-offers-tab > li {
  flex: 1 1 auto !important;
  text-align: center !important;
  display: flex;
  justify-content: center;
}

.fpo-offers-tab {
  border: 0px !important;
}

.fpo-offers-tab > li > button {
  border-top: 0px !important;
  border-left: 0px !important;
  border-right: 0px !important;
  padding: 0px 0px 8px 0px;
  background-color: transparent !important;
  border-bottom: 2px inset var(--nav-border) !important;
  border-radius: 0px !important;
  text-transform: uppercase;
  font-size: 14px;
  font-weight: 700;
  font-family: gilroy-bold;
  width: 100%;
  color: var(--muted);
}

.accept-offer-icon {
  background: var(--secondary);
  padding: 6px;
  padding-left: 8px;
  padding-right: 8px;
  border-radius: 50px;
  margin-right: 8px;
}

.fpo-offers-tab > li > button.active .accept-offer-icon {
  background: var(--green);
  color: var(--white);
}

.fpo-offers-tab > li > button.active {
  color: var(--green) !important;
  border-bottom: 2px inset var(--green) !important;
}

.fpo-apply-load-title {
  font-size: 16px;
  font-weight: 600;
  margin-bottom: 16px;
}

.absolute-center {
  position: absolute;
  top: 0px;
  left: 0px;
  background-color: transparent;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.requested-document-upload .dropdown-menu {
  min-width: unset !important;
  padding: 0px !important;
}

.equested-document-upload .dots {
  color: var(--secondary) !important;
}

.requested-document-upload .dropdown-menu .dropdown-item {
  color: var(--secondary) !important;
  color: #00000080 !important;
  opacity: 1;
  padding: 6px 12px !important;
  margin: 0px !important;
}

.requested-document-upload .dropdown-menu .dropdown-item:active {
  background-color: transparent !important;
}

.request-doc-save-btn {
  color: var(--secondary) !important;
  text-decoration: underline;
  font-weight: 600;
  font-size: 12px;
}

.br-10 {
  border-radius: 10px !important;
}

.br-15 {
  border-radius: 15px !important;
}

.fpo-filter-pills {
  background-color: var(--dark-white) !important;
  border-radius: 10px;
  margin: 0px 15px 15px 0px;
  white-space: nowrap;
  padding: 7px 17px;
}

.fpo-filter-pills span {
  color: var(--black) !important;
  opacity: 0.6;
  font-size: 13px !important;
  font-weight: bold;
  white-space: nowrap;
}

.fpo-filter-pills.active {
  background-color: #d2ecfa !important;
  border: 1px solid #c0dffd !important;
}

.modal-90w {
  width: 90vw !important;
  max-width: 90vw !important;
}

.tenure-class {
  max-width: 100%;
}
