.action-btn {
  position: absolute;
  right: 0px;
  top: 0px;
  outline: none;
  border: 0px;
  background-color: var(--blue);
  padding: 8px 18px;
  font-size: 12px;
  font-weight: bold;
  color: #fff;
  border-bottom-left-radius: 12px;
  border-top-right-radius: 3px;
}

.action-btn:disabled {
  background-color: #b5b5c3;
  color: #fff;
}

.back-btn {
  color: var(--secondary);
  font-weight: bold;
  border: none;
  background: transparent;
  display: flex;
  align-items: center;
}
