.sam-vintage-chart-container {
  background-color: var(--bg-2);
  border-radius: 12px;
  padding: 20px;
  margin-bottom: 20px;
}

.chart {
  width: 100% !important;
  height: 300px !important;
  display: block;
}

.financial_performance_tabs li button {
  border: 0px !important;
  background-color: transparent;
  margin: 0px !important;
}

.financial_performance_tabs li button.active {
  font-weight: 700 !important;
}

.financial_performance_tabs {
  border-bottom: 1px solid var(--nav-border) !important;
}

.tooltip-body .series-name {
  font-weight: normal;
  opacity: 0.6;
  display: inline-block;
  line-height: 1.5;
  padding-right: 10px;
  width: auto;
}

.chart-tooltip-table {
  margin: 0px !important;
}

.chart-tooltip-table.table-borderless > tbody > tr > td,
.chart-tooltip-table.table-borderless > tbody > tr > th,
.chart-tooltip-table.table-borderless > tfoot > tr > td,
.chart-tooltip-table.table-borderless > tfoot > tr > th,
.chart-tooltip-table.table-borderless > thead > tr > td,
.chart-tooltip-table.table-borderless > thead > tr > th,
.chart-tooltip-table.table-borderless > tbody > tr {
  border: none !important;
  padding: 5px 5px !important;
}

.dxg-tooltip {
  z-index: 99999;
}

.ind-map-container > div {
  transform: translateX(-40%);
}

.map-chart-table > tbody > tr > td,
.map-chart-table > tbody > tr > th,
.map-chart-table > tfoot > tr > td,
.map-chart-table > tfoot > tr > th,
.map-chart-table > thead > tr > td,
.map-chart-table > thead > tr > th,
.map-chart-table > tbody > tr {
  border: none !important;
}

.map-chart-table > tbody > tr > td {
  padding: 7px 0px !important;
}

.text-primary {
  color: var(--blue) !important;
}
