:root {
  --primary-bg: #fbf9f6;
  --white: #ffffff;
  --black: #000000;
  --shadow-primary: #0000001a;
  --swiper-theme-color: #ffffff !important;
  --green: #20d489;
  --dark-white: #fbf9f6;
  --blue: #00a3ff;
  --secondary: #b5b5c3;
  --shadow-border: #00000029;
  --circle: #f3f6f9;
  --violet: #dabcff;
}

.swiper-pagination-bullet {
  background: var(--white) !important;
}

.bg-white {
  background-color: var(--white);
}

.bg-circle {
  background-color: var(--circle);
}
.z-100 {
  z-index: 100;
}
.cursor-pointer {
  cursor: pointer;
}
