.sansaction-document-tabs {
  border: 0px !important;
}
.sansaction-document-tabs > li > button {
  border: 0px !important;
  background: var(--tab-default);
  color: var(--black);
  border-radius: 12px !important;
  margin-right: 8px;
}

.sansaction-document-tabs > li > button.active {
  background: var(--tab-blue) !important;
}

.sansaction-document-tabs + .tab-content table thead tr > th {
  border-top: 0px !important;
}

