.file-input {
  background: #f7fcff 0% 0% no-repeat padding-box;
  border: 1px dashed #c8eafd;
  display: flex;
  align-items: center;
  width: 100%;
  border-radius: 10px;
  padding: 6px 12px;
  font-size: 14px;
  color: #000000;
  font-weight: 400px;
}

.file-input :hover {
  cursor: pointer;
}

.file-input img {
  width: 18px;
  margin-right: 20px;
}
