:root {
  --primary-bg: #fbf9f6;
  --white: #ffffff;
  --black: #000000;
  --shadow-primary: #0000001a;
  --swiper-theme-color: #ffffff !important;
  --green: #20d489;
  --dark-white: #fbf9f6;
  --blue: #00a3ff;
  --secondary: #b5b5c3;
  --shadow-border: #00000029;
  --circle: #f3f6f9;
  --violet: #dabcff;
  --muted: #626566;
  --bg-muted: #f4fbff;
  --bg-muted-secondary: #6e6e6e;
  --shadow: #0000000d;
  --tab-blue: #b6e3fc;
  --tab-default: #f8f8f8;
  --light-blue: #e7f6ff;
  --bluish: #e7f6ff;
  --red: #f23838;
  --accepted: #20d48980;
  --rejected: #f2383880;
  --nav-border: #a5a5a5;
  --updated: #8423ff80;
  --samunnati-secondary: #fec232;
  --icon-color: #cddee8;
  --bg-2: #f4fbff;
}

.swiper-pagination-bullet {
  background: var(--white) !important;
}

.bg-white {
  background-color: var(--white);
}

.bg-circle {
  background-color: var(--circle);
}

.swiper-pagination-bullet {
  background: var(--white) !important;
}
.bg-blue {
  background: var(--bluish);
}
.link {
  color: var(--blue);
}
.text-blue {
  color: var(--blue);
}

.bg-muted {
  color: var(--bg-muted-secondary);
}
.bg-white {
  background-color: var(--white);
}
.bg-light-blue {
  background: var(--light-blue);
}

.bg-light-muted {
  background: var(--bg-muted);
}
.bg-circle {
  background-color: var(--circle);
}

.nav-pills .nav-link.active,
.nav-pills .show > .nav-link {
  background: var(--tab-blue);
  color: var(--black);
}

.d-box {
  background: var(--primary-bg);
  padding: 20px;
  padding-bottom: 10px;
}
