@import "../../styles/Font";
@import "../../styles/Base";

.p-button {
    background-color: $primary-color;
    border: 1px solid $primary-color;
}

.p-button:enabled:hover,
.p-button:not(button):not(a):not(.p-disabled):hover {
    background: $primary-color;
    color: $white;
    border-color: $primary-color;
}
