.fpo-offers-accordion .accordion-item {
  box-shadow: 0px 3px 6px var(--secondary);
  border-radius: 12px !important;
  border: 1px solid #dee2e6 !important;
  background-color: var(--white);
}

.fpo-offers-accordion .dflex-center {
  display: flex;
  align-items: center;
  justify-content: center;
}
.fpo-offers-accordion button.accordion-button {
  display: block;
  width: 100%;
  border: none;
  padding: 0px !important;
  background: transparent !important;
}

.fpo-offers-action-btns {
  padding: 10px 30px 10px 30px;
  font-weight: 600;
}

.p-16 {
  padding: 16px !important;
}

.mb-n40 {
  margin-bottom: -40px;
}

.dflex-offer {
  display: flex;
  align-self: end;
  justify-content: end;
}

.dflex-offer > div {
  display: flex;
  align-items: center;
}

.bg-accepted,
.offers-list.won {
  background: #ffffff 0% 0% no-repeat padding-box;
  box-shadow: 0px 3px 6px var(--accepted);
}
.bg-rejected,
.offers-list.reject {
  background: #ffffff 0% 0% no-repeat padding-box;
  box-shadow: 0px 3px 6px var(--rejected);
}

.bg-updated {
  background: #ffffff 0% 0% no-repeat padding-box;
  box-shadow: 0px 3px 6px var(--updated);
}

.circle {
  min-width: 26px;
  height: 13px;
  border-radius: 13px;
}
