.sam-analysis-tab-container.nav-tabs {
  border-bottom: 0px;
  display: flex;
  justify-content: space-between;
}

.sam-analysis-tab-container.nav-tabs .nav-item {
  flex: 1 1 auto;
  padding: 16px;
}

.sam-analysis-tab-container.nav-tabs .nav-item button {
  border: 0px;
  background-color: var(--tab-default);
  border-radius: 12px;
  font-size: 16px;
  font-weight: 600;
  letter-spacing: 0px;
  color: var(--black);
  text-transform: uppercase;
  opacity: 0.7;
  width: 100%;
  text-align: center;
}

.sam-analysis-tab-container.nav-tabs .nav-item button:hover,
.sam-analysis-tab-container.nav-tabs .nav-item button.active {
  opacity: 1;
  background-color: var(--tab-blue);
}

.row {
  margin-left: 0px;
  margin-right: 0px;
}

.document-image {
  width: 121px;
  height: 101px;
}

.sam-text-box {
  background-color: var(--light-blue);
  border-bottom-left-radius: 12px;
  border-bottom-right-radius: 12px;
  padding: 5px;
}

.sam-container {
  color: var(--black) !important;
  text-decoration: none !important;
}

.dropdown-toggle::after {
  display: inline-block;
  margin-left: 0px !important;
  vertical-align: 0px !important;
  content: none;
  border: 0px !important;
}

.dots-container {
  position: relative;
  margin-right: 5px;
}

.dots-container > a {
  color: #6c757d;
  padding: 2px 5px;
  font-size: 12px;
}

.dots-container .dropdown-menu > .dots-link {
  font-size: 12px;
  padding: 1px 15px;
}

.sam-upload-container .sam-upload-text-box {
  background-color: var(--blue);
  width: 120px;
  height: 40px;
  padding: 0px;
  border: none;
}

.sam-upload-container .sam-icon-container {
  background-color: var(--bg-muted);
  width: 120px;
  height: 100px;
  padding: 0px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
}

.sam-upload-container .sam-file-icon {
  color: var(--icon-color);
  width: 27px;
  height: 36px;
}

.sam-upload-container .upload-text {
  font-size: 9px !important;
  font-weight: 700;
  color: #fff;
}
