.fpo-card-container i {
  color: #cddee8;
}

.fpo-card-container .fpo-title {
  font-size: 14px;
  font-weight: bold;
  color: #000000;
  text-transform: capitalize !important;
}

.fpo-card-container .fpo-info-container p {
  font-size: 13px;
  vertical-align: bottom;
}

.fpo-card-container .fpo-info-container i {
  font-size: 18px !important;
  vertical-align: bottom;
}

.fpo-card-container .fpo-info-container > .row {
  margin-top: 10px;
}

// checkbox
.fpo-check-box {
  width: 25px;
  height: 25px;
  background: var(--icon-color);
  border-radius: 6px;
}

.custom-label {
  display: inline-block;
  padding-left: 2.5em;
  position: relative;
  cursor: pointer;
}
.select-input[type="checkbox"] {
  opacity: 0;
  width: 0px;
  height: 0px;
}
label {
  transition: color 80ms linear;
}
label .fpo-check-box {
  transition: background-color 200ms linear;
}
label .fpo-check-box:hover {
  cursor: pointer;
}
label .fpo-check-box path {
  transition: all 80ms linear;
  stroke: #fff;
  transform: translate(10px, 10px) scale(0.8);
}
.select-input[type="checkbox"]:checked ~ label .fpo-check-box path {
  stroke: var(--green);
}

.select-input[type="checkbox"]:checked ~ label .fpo-check-box {
  background: var(--bg-muted);
}

.in-active-application-card {
  background-color: transparent;
  border: none;
}

.remarks-card-container {
  padding: 14px 20px 14px 14px;
  background-color: #fcfeff;
  border: 1px solid #dfecf2;
  border-radius: 12px;
}

.remarks-card-container ol {
  list-style: disc;
  padding-left: 14px !important;
  color: #6a7e88;
}
