/* primary */

.btn:disabled {
  cursor: not-allowed;
}

.btn-primary {
  background: var(--green) 0% 0% no-repeat padding-box;
  border: 1px solid var(--green);
}

.btn-primary[disabled] {
  background: var(--green) 0% 0% no-repeat padding-box;
  border: 1px solid var(--green);
}

.btn-primary:hover {
  background: var(--dark-white) 0% 0% no-repeat padding-box;
  border: var(--green) 1px solid;
  color: var(--green) l;
}

.btn-primary.focus,
.btn-primary:focus {
  color: #fff;
  background-color: var(--dark-white);
  border: 1px solid var(--green);
  color: var(--green);
  box-shadow: none !important;
}

.btn-primary:not(:disabled):not(.disabled).active,
.btn-primary:not(:disabled):not(.disabled):active,
.show > .btn-primary.dropdown-toggle {
  color: var(--green);
  background-color: var(--dark-white);
  border: 1px solid var(--green);
}

/* secondary */

.btn-secondary {
  background: var(--secondary) 0% 0% no-repeat padding-box;
  border: 1px solid var(--secondary);
}

.btn-secondary:hover {
  background: var(--dark-white) 0% 0% no-repeat padding-box;
  border: var(--secondary) 1px solid;
  color: var(--secondary) l;
}

.btn-secondary.focus,
.btn-secondary:focus {
  color: #fff;
  background-color: var(--dark-white);
  border: 1px solid var(--secondary);
  color: var(--secondary);
  box-shadow: none !important;
}

.btn-secondary:not(:disabled):not(.disabled).active,
.btn-secondary:not(:disabled):not(.disabled):active,
.show > .btn-secondary.dropdown-toggle {
  color: var(--secondary);
  background-color: var(--dark-white);
  border: 1px solid var(--secondary);
}

