/* compare fpo tab */
.compare-fpo-table > tbody > tr > td,
.compare-fpo-table > tbody > tr > th,
.compare-fpo-table > tfoot > tr > td,
.compare-fpo-table > tfoot > tr > th,
.compare-fpo-table > thead > tr > td,
.compare-fpo-table > thead > tr > th {
  border: none;
}
.compare-fpo-table .border-none {
  border: 0px !important;
}
.compare-fpo-table,
.compare-fpo-table tbody th {
  border: 1px solid #e5eaee !important;
  font-size: 14px;
  padding: 12px 20px !important;
}

.compare-fpo-table {
  border: 0px !important;
}

.compare-fpo-table tr:last-child th:last-child {
  border-top-left-radius: 12px !important;
}

.single-line {
  white-space: nowrap;
}

.bank-request-badge {
  padding: 5px 10px;
  font-size: 12px;
  text-transform: capitalize;
  color: #ffffff;
  font-weight: 100;
  border-radius: 12px;
}
.bank-request-badge.sent {
  background-color: var(--samunnati-secondary) !important;
}

.bank-request-badge.check {
  background-color: var(--violet) !important;
}

.bank-request-table
  tbody
  tr
  td:not(.bank-request-table tbody tr td:nth-child(3)) {
  font-size: 12px;
  font-weight: bold;
  opacity: 0.5;
}
