  padding: 10px 22px;
  letter-spacing: 1px;
  border: 0px !important;
}
.cancel-application-btn:active {
  color: white !important;
}
.cancel-application-btn:hover {
  color: white !important;
}
.cancel-application-btn:focus {
  color: white !important;
}
