.row {
  padding: 0;
  margin: 0;
}

/* background colors */
.primary-bg {
  background-color: var(--primary-bg);
}
.badge-violet {
  background-color: var(--violet);
}

/* tables */
.table thead th {
  border-bottom: none;
  border-top: none;
  padding: 0px;
  text-align: left;
  padding-bottom: 16px;
}
.draft-table td {
  width: 100% !important;
}
.table td,
.table th {
  border-bottom: none !important;
  border-top: 1px #0000001a solid !important;
  padding: 0px;
  text-align: left;
  padding-top: 16px !important;
  padding-bottom: 16px !important;
}
.table tr:last-child {
  border-bottom: 0.5px #0000001a solid;
  padding-bottom: 16px;
}
/* display */
.flex {
  display: flex;
}
.dflex {
  display: flex;
  align-self: center;
  justify-content: center;
}
.align-item-center {
  display: flex;
  align-self: center;
}

.align-item-self-center {
  align-self: center;
}

/* height */
.h100 {
  height: 100vh;
  width: 100%;
}
.w100 {
  height: 100px;
  width: 400px;
}

/* .navbar */
.dropdown-toggle::after {
  display: none;
}
.text-decoration-none {
  text-decoration: none !important;
}
/* Font Awesome Icons have variable width. Added fixed width to fix that.*/
.icon-width {
  width: 2rem;
}

/* remove blue line from from control */
.form-control:focus {
  color: #495057;
  background-color: #fff;
  border-color: none !important;
  outline: 0;
  box-shadow: none !important;
}
/* .conatiner */
@media (min-width: 1200px) {
  .container,
  .container-lg,
  .container-md,
  .container-sm,
  .container-xl {
    max-width: 1380px !important;
  }
}

.bank-loan-tabs,
.bank-loan-tabs li button {
  border: 0px !important;
}

.bank-loan-tabs li button {
  background-color: var(--tab-default);
  border-radius: 12px !important;
}

.bank-loan-tabs li button.active {
  background-color: var(--tab-blue) !important;
  font-weight: 600;
}

.searchInputWrapper {
  position: relative;
}

.searchInput {
  width: 100%;
  height: calc(1.5em + 0.75rem + 2px);
  padding: 0 1rem;
  border-radius: 12px;
  transition: transform 0.1s ease-in-out;
  border: 1px solid var(--secondary);
}

::placeholder {
  color: #a1a1a1;
}

/* hide the placeholder text on focus */
:focus::placeholder {
  text-indent: -999px;
}

.searchInput:focus {
  outline: none;
  transform: scale(1.1);
  transition: all 0.1s ease-in-out;
}

.text-bold {
  font-weight: bold;
}

.bank-loan-view-link {
  color: var(--blue);
  background-color: transparent !important;
  border: 0px;
}

.br-3 {
  border-radius: 4px !important;
}

.fpo-loan-apply-container {
  position: relative;
}

.fpo-loan-apply-container ul {
  position: absolute;
  right: 0px;
  top: 5px;
}

.fpo-dashboard-loan-tabs,
.fpo-dashboard-loan-tabs li button {
  border: 0px !important;
}

.fpo-dashboard-loan-tabs li button {
  background-color: var(--bg-2);
  border-radius: 12px !important;
  color: var(--secondary);
  font-size: 14px;
  font-weight: 400 !important;
  margin: 0px 10px;
}

.fpo-dashboard-loan-tabs li button.active {
  background-color: var(--blue) !important;
  font-weight: 600;
  color: var(--white) !important;
}

.fpo-loan-swiper-content-container {
  background-color: var(--dark-white);
  border-radius: 12px;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 230px;
}

.fpo-loan-swiper-content-container .title {
  font-size: 16px;
}

.fpo-loan-swiper-content-container .btn {
  border-bottom-left-radius: 14px;
  border-bottom-right-radius: 14px;
}

.swiper_prev_button,
.swiper_next_button {
  position: absolute;
  top: 40%;
  z-index: 2;
  border-radius: 100%;
  cursor: pointer;
  width: 45px;
  height: 45px;
}

.swiper_prev_button {
  left: -20px;
}

.swiper_next_button {
  right: -20px;
}

.fs-16 {
  font-size: 16px !important;
}

.w-30 {
  width: 30%;
}

.w-20 {
  width: 20%;
}

.f-24 {
  font-size: 24px !important;
}

.docs-modal-doc-title {
  font-size: 14px !important;
  border-bottom: 2px solid #e5eaee;
  width: 100%;
  padding-bottom: 5px;
}

.document-review-sub-modal {
  z-index: 999999999;
  padding-left: 0px !important;
  background-color: rgba(0, 0, 0, 0.6);
}

.draft-info-legends {
  position: absolute;
  top: 0px;
  width: 50%;
  left: 50%;
  align-items: center;
  height: 100%;
}

.draft-info-legends > p > span {
  display: inline-block;
  width: 9px;
  height: 9px;
  margin-right: 5px;
  border-radius: 12px;
}

.draft-info-legends > p > span.draf {
  background-color: black;
  opacity: 0.5;
}

td.draft {
  color: black;
  opacity: 0.5;
  font-weight: bold;
}

td.re-apply,
.re-apply {
  color: var(--samunnati-secondary);
  font-weight: bold;
}

.draft-info-legends > p > span.re-apply {
  background-color: var(--samunnati-secondary);
}

